w-text-block::part(root) {
  all: initial;
  font-family: "Helvetica Neue", arial, sans-serif;
  font-weight: 400;
  color: #444;
}
w-text-block::part(primary) {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #323130;
  font-weight: 400;
  font-size: 20px;
}
w-text-block::part(secondary) {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #605e5c;
  font-weight: 400;
  font-size: 14px;
}